.footer .footerLinks {
  background-color: #515358;
}

.footerLinksRow,
.footerMapRow,
.footerUtilityRow {
  padding: 0 10px;
  width: 1175px;
  margin: 0 auto;
}

.footerMapRow,
.footerLinksRow,
.footerUtilityRow {
  width: 940px;
}

.footerLinksRow a {
  display: block;
  line-height: 26px;
  color: #fff;
}
.footerLinksRow a,
.footerMapRow a,
.footerUtilityRow a {
  text-decoration: none;
}

.footerLinks,
.footerMap,
.footerUtility {
  background-color: #3e4045;
  width: 100%;
  float: left;
  min-width: 960px;
}

.footer .footerLinksRow li.contact {
  background-image: url("../assets/contact.png");
}

.footer .footerLinksRow li.branch {
  background-image: url("../assets/branch.png");
}

.footer .footerLinksRow li {
  float: left;
  font-size: 112.5%;
  line-height: 26px;
  padding: 21px 29px 19px 52px;
  background-position: 20px center;
  background-repeat: no-repeat;
}

.footerMapRow {
  padding: 37px 10px 36px 10px;
  background: url("../assets/footer.gif") center bottom no-repeat;
  width: 950px;
}

.footerMap .footerMapRow .column.last {
  padding-right: 0;
}
.footerMap .footerMapRow .column {
  padding: 0 10px 0 0;
  width: 145px;
}
.footerMap .column {
  float: left;
  padding: 0 25px 0 0;
  width: 180px;
}

.footerMap h2 {
  font-size: 112.5%;
  font-weight: bold;
  line-height: 28px;
  padding: 0 0 16px;
  color: #fff;
}

.footerMap ul {
  padding: 0 15px 0 0;
}

.footerLinksRow ul {
  float: left;
  margin: 0 0 0 -20px;
}

.footerMap li {
  font-size: 93.75%;
  line-height: 18px;
  padding: 0 0 9px;
  color: #ccc;
}

.footerMap li a {
  color: #ccc;
  line-height: 18px;
}

.footerLinksRow:after,
.footerMapRow:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.footerMap h2 a {
  color: #fff;
  line-height: 28px;
}

.footerUtility ul {
  float: left;
  padding: 21px 0 48px;
}

.footerUtility ul li,
.footerUtility p {
  float: left;
  padding: 0 18px 0 0;
  font-size: 68.75%;
  color: #fff;
  line-height: 16px;
}

.footerUtility ul li a,
.footerUtility p a {
  color: #fff;
  line-height: 16px;
}

.footerUtility p {
  float: right;
  padding: 21px 0 48px;
}
