.mainTopWrapper {
  width: 100%;
}
.mainTopUtility {
  width: 100%;
  min-width: 960px;
  background-color: #515358;
  min-height: 33px;
}

.mainTopUtility {
  min-width: 625px;
}
body h1 {
  left: -9999px;
  position: absolute;
}
.mainTopUtilityRow {
  margin: 0 auto;
  z-index: 10;
  position: relative;
  width: 940px;
  padding: 0 10px;
}
.mainTopUtilityRow:after {
  content: ".";
  display: none;
  height: 0;
  clear: both;
  visibility: hidden;
}
#tabs {
  float: left;
}
#tabs li {
  font-size: 75%;
  padding: 10px 15px 0 0;
  float: left;
  color: #fff;
  white-space: nowrap;
  position: relative;
}
#tabs li.skipLink {
  z-index: 1;
  padding: 10px 0 0;
}
#tabs li.on,
#tabs li.currentBreadcrumb {
  font-weight: bold;
}
#tabs li.currentBreadcrumb {
  padding: 10px 15px 10px 0;
  margin: 0 0 0 -9px;
}
#tabs li a {
  line-height: 13px;
  height: 13px;
  padding: 0 0 10px;
  color: #fff;
  display: block;
  text-decoration: none;
}
#tabs li.skipLink a {
  position: absolute;
  z-index: 400;
  padding: 0 150px 10px 0;
}
#tabs li a.skip {
  left: -9999px;
}
#tabs li a.skip:active,
#tabs li a.skip:focus,
#tabs li a.ieSkip {
  left: 0;
  background: #515358;
}
#tabs li a:hover,
#tabs li a:active,
#tabs li a:focus {
  text-decoration: underline;
}
#tabs li.on a {
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/masthead/bg_arrow.gif")
    no-repeat center bottom;
}
#tabs li.currentBreadcrumb a {
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/masthead/toptab_darkgrey_arrow.gif")
    left center no-repeat;
  padding: 0 0 0 14px;
}
#siteControls {
  float: right;
}
#langList,
#langList ul {
  float: left;
}
#langList {
  padding: 0 4px 0 0;
}
#langList li {
  display: block;
  font-size: 75%;
  float: left;
  padding: 10px 15px 10px 0;
}
#langList li a {
  line-height: 13px;
  height: 13px;
  display: block;
  color: #fff;
  text-decoration: none;
}
#langList li.selected {
  font-weight: bold;
}
#langList li a:hover,
#langList li a:active,
#langList li a:focus {
  text-decoration: underline;
}
.locale {
  padding: 0 0 0 1px;
  float: left;
  position: relative;
  z-index: 300;
}
.locale a.dropDownLink,
.locale .dropDownClosing a.dropDownLink,
.locale .dropDownClosed a.dropDownLink,
.locale .dropDownOpening a.dropDownLink,
.locale .dropDownOpen a.dropDownLink {
  display: block;
  text-decoration: none;
  color: #fff;
  padding: 10px 0 10px 10px;
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/masthead/bg_gradient.gif")
    left bottom repeat-x;
}
.locale .dropDownOpening a.dropDownLink,
.locale .dropDownOpen a.dropDownLink {
  background: #393b3e;
}
.locale a.dropDownLink:hover,
.locale a.dropDownLink:active,
.locale a.dropDownLink:focus {
  text-decoration: underline;
}
.locale a.dropDownLink span,
.locale .dropDownClosing a.dropDownLink span,
.locale .dropDownClosed a.dropDownLink span,
.locale .dropDownOpening a.dropDownLink span,
.locale .dropDownOpen a.dropDownLink span {
  background-image: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/masthead/locale.gif");
  background-repeat: no-repeat;
  background-position: right top;
  padding: 0 29px 0 0;
  display: block;
  font-size: 75%;
  line-height: 13px;
  height: 13px;
}
.locale .dropDownOpening a.dropDownLink span,
.locale .dropDownOpen a.dropDownLink span {
  background-position: right bottom;
}
.locale a.dropDownLink span span.flag {
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/masthead/flag.gif")
    left 1px no-repeat;
  padding: 0 0 0 0px;
  font-size: 100%;
}
#countrySelectorContent {
  display: none;
  background-color: #393b3e;
  float: left;
}
.no-js #countrySelectorContent {
  display: block;
  width: 100%;
}
.locale #countrySelectorContent {
  top: 33px;
  right: 0;
  position: absolute;
  width: 856px;
  padding: 5px;
}
.locale .regionTabs {
  float: right;
  background-color: #515358;
  width: 273px;
  min-height: 332px;
}
.locale .regionTabs li {
  font-size: 150%;
  color: #fff;
}
.locale .regionTabs li a {
  line-height: 28px;
  text-decoration: none;
  color: #fff;
  padding: 15px 12px 13px 36px;
  border-bottom: 1px solid #393b3e;
  display: block;
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/masthead/bg_country_arrow.gif")
    left center no-repeat;
}
.locale .regionTabs li a.americas {
  border-bottom: 1px solid #515358;
}
.locale .regionTabs li.selected a {
  background: #393b3e;
}
.locale .regionTabs li a:hover,
.locale .regionTabs li a:active,
.locale .regionTabs li a:focus {
  text-decoration: underline;
}
.no-js .regions {
  margin: 0 auto;
  width: 1200px;
  padding: 25px 0 0 25px;
}
.locale .regions {
  width: 564px;
  padding: 15px 0 0 19px;
  float: right;
}
.region,
.navList,
.nav {
  float: left;
  background-color: #393b3e;
}
.no-js .region,
.no-js .navList {
  width: 1200px;
}
.no-js .region {
  padding: 0 0 25px;
}
.locale .region,
.locale .navList {
  width: 564px;
}
.no-js .region h2 {
  color: #fff;
  font-size: 150%;
  font-weight: normal;
  line-height: 28px;
  padding: 0 0 15px;
}
.locale .region h2 {
  display: none;
}
.no-js .nav {
  width: 375px;
  padding: 0 25px 0 0;
}
.no-js #middleEast .nav {
  width: 575px;
}
.locale .nav {
  width: 164px;
  padding: 0 24px 0 0;
}
.locale #middleEast .nav {
  width: 258px;
}
.nav li {
  font-size: 93.75%;
  line-height: 18px;
  padding: 8px 0 6px;
  border-bottom: 1px dotted #4d4f52;
  color: #fff;
}
.nav li.last {
  border-bottom: none;
}
.nav li.multiTop {
  border-bottom: none;
  padding: 8px 0 0;
}
.nav li.multiMiddle {
