.alertBoxWrapper {
  display: none;
}
.confirmAlertSecondary {
  width: 100%;
  position: relative;
  padding-top: 20px;
}
.alertBox {
  width: 100%;
  position: relative;
}
.alertBox .alertBoxInner {
  border: 3px solid #ffcbc9;
  padding: 13px 20px 8px 117px;
  min-height: 88px;
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/AlertBox/icon-error-large.gif")
    no-repeat scroll 30px 50% #fff2f1;
}
.alertBox .alertBoxInnerData {
  padding: 113px 20px 8px 44px;
  min-height: 58px;
}
.alertBox .alertBoxInner:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.alertBox .alertBoxInner p {
  color: #000;
  padding-bottom: 5px;
}
.alertBox .alertBoxInner ul {
  padding-bottom: 5px;
}
.alertBox .alertBoxInner li {
  list-style: disc inside;
  font-size: 1.4em;
  line-height: 18px;
}
.alertBox .alertBoxInner h3.title {
  font-size: 1.5em;
  font-weight: bold;
  line-height: 22px;
  padding-bottom: 0;
}
.alertBox .alertBoxInnerData h3.title {
  font-size: 1.6em;
  font-weight: bold;
  line-height: 22px;
  padding-bottom: 0;
}
.infoAlert .alertBoxInner {
  background-image: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/AlertBox/icon-informative-large.gif");
  background-color: #fffcf5;
  border-color: #fff3d8;
}
.messageAlert .alertBoxInner {
  background-image: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/AlertBox/additionalIcon-lrg.gif");
  background-color: #f6f6f6;
  border-color: #dbdbdb;
}
.confirmAlertSecondary .alertBoxInnerData {
  background-color: #f9fcf7;
  border-color: #e7f5e0;
  padding-top: 20px;
}
.confirmAlert .alertBoxInner {
  background-image: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/AlertBox/icon-confirmation-large.gif");
  background-color: #f9fcf7;
  border-color: #e7f5e0;
}

.alertBox .iconText {
  color: #297500;
  font-size: 1.1em;
  left: 100px;
  position: absolute;
  text-align: center;
  text-transform: uppercase;
  top: 136px;
  width: 160px;
}

.confirmation {
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/AlertBox/successful.gif")
    no-repeat scroll 0 0 transparent;
  border: 1px solid #e3e3e3;
  box-shadow: 0 0 4px #dddddd;
  float: left;
  height: auto;
  margin-top: 17px;
  padding: 0;
  width: 938px;
}
.confirmation .alertBoxInner {
  border: 0;
  background: none;
  padding: 30px 40px 20px 294px;
  min-height: 118px;
  _height: 118px;
}
.confirmation h3 {
  font-size: 2.2em;
  line-height: 30px;
  margin: 0;
  padding: 0;
}
.confirmation p {
  color: #4c4c4c;
  font-size: 1.6em;
  line-height: 32px;
  padding: 0;
}
.confirmation .alertBoxInner .expired {
  color: #db0011;
  padding: 0 0 6px;
}
.timedAlert {
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/AlertBox/timed.gif")
    no-repeat scroll 0 50% transparent;
}
.timedAlert .iconText {
  color: #db0011;
}
.timedAlert .iconTime {
  color: #db0011;
  font-size: 3em;
  left: 133px;
  position: absolute;
  text-align: center;
  top: 57px;
  width: 100px;
}
.timedAlert .iconTime strong {
  font-size: 1.5em;
}

.infoAlert {
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/AlertBox/info.gif")
    no-repeat scroll 0 50% transparent;
}
.infoAlert h3 {
  font-size: 2.1em;
}
.infoAlert h3.infoPadding {
  padding: 14px 0;
}
.infoAlert .alertBoxInner p,
.infoAlert .alertBoxInner li {
  color: #585858;
}
.errorAlert {
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/AlertBox/error.gif")
    no-repeat scroll 0 50% transparent;
}
.errorAlert .iconText {
  color: #db0011;
  font-size: 1.2em;
  font-weight: bold;
}
.alertBox-ext01 .alertBoxInner {
  padding-top: 18px;
}

.confirm .alertBoxInner {
  padding: 13px 20px 8px 50px;
  min-height: 110px;
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/StepTracker/dot-stepTracker-completed.gif")
    no-repeat scroll 15px 20% #ecf1df;
}

.confirm .alertBoxInner p.title {
  font-size: 1.6em;
  font-weight: bold;
  line-height: 22px;
  padding-bottom: 0;
  color: #33a000;
  padding-top: 10px;
}
.confirm .alertBoxInner p {
  font-weight: bold;
}

.cancel .alertBoxInner {
  padding: 13px 20px 8px 50px;
  min-height: 125px;
  background: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/AlertBox/icon-yellow.gif")
    no-repeat scroll 15px 20% #f8e8b8;
}
.cancel .alertBoxInner p.title {
  font-size: 1.8em;
  font-weight: bold;
  line-height: 22px;
  padding-bottom: 0;
  padding-top: 10px;
}
.cancel .alertBoxInner p {
  font-weight: bold;
}
a.colorRed {
  color: red;
}
