  background-image: url("https://www.security.hsbc.co.uk/gsp/saas/Components/default/resources/script/libraries/hsbc/widget/themes/ursula/images/AlertBox/infoCircle.png") !important;
  background-position: 17px 17px !important;
  background-color: #fffcf5 !important;
  border-color: #fff3d8 !important;
