.pageHeaderBg {
  background-color: #f8f8f8;
  min-width: 960px;
}

.pageHeaderBg .pageHeading {
  background: url("../assets/page-heading-gradient.png") repeat-x left top
    transparent;
  padding-top: 7px;
  padding-bottom: 16px;
}

.pageHeaderBg .pageHeadingInner {
  width: 100%;
  margin: 0 auto;
  padding: 10px 0 0;
}

.pageHeaderBg h2 {
  font-size: 1.5em;
  font-weight: normal;
  line-height: 1em;
  margin: 0 auto;
  padding: 0 10px;
  max-width: 940px;
}
