.header {
  width: 100%;
  background-color: #fff;
}

.header .topUtility {
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-width: 960px;
  background-color: #515358;
  min-height: 33px;
}

.header .topUtility .mainTopUtilityRow {
  margin: 0 auto;
  z-index: 10;
  position: relative;
  width: 940px;
  display: flex;
  padding: 0 10px;
  justify-content: space-between;
  align-items: center;
}

.header .topUtility .mainTopUtilityRow li a {
  font-size: 75%;
  padding-left: 15px;
  color: #fff;
  white-space: nowrap;
  position: relative;
}

.header .topUtility .mainTopUtilityRow li a:hover {
  text-decoration: underline;
}

.header .topUtility .mainTopUtilityRow li a.active {
  font-weight: bold;
}

.header .topUtility .mainTopUtilityRow .on {
  background: url("../assets/arrow.gif") no-repeat center center;
  position: absolute;
  z-index: 9999;
  bottom: -2px;
  width: 10px;
  height: 10px;
  left: 42px;
}

.header .topUtility .mainTopUtilityRow ul {
  display: flex;
  color: white;
  align-items: center;
}

.mainTopNavigation {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 20px 10px;
  margin: 0 auto;
  position: relative;
  width: 940px;
  z-index: 2;
}

.tabs a,
.siteControls a {
  text-decoration: none;
  font-size: 75%;
}

.siteControls a:nth-child(1) {
  margin-left: 15px;
}

.siteControls a:nth-child(1):hover {
  text-decoration: underline;
}

.mainTopNavigation .logo {
  padding-right: 24px;
}

.mainTopNavigation .selections {
  display: flex;
  flex: 1 1 0px;
}

.selections a {
  text-decoration: none;
}

.mainTopNavigation .selections nav ul.mainul {
  display: flex;
}

.mainTopNavigation .selections nav ul.mainul li.mainli {
  background: url("../assets/divider.gif") left center no-repeat;
  padding: 24px 10px 18px;
}

.mainTopNavigation .selections nav ul.mainul li.mainli a.maina {
  white-space: nowrap;
  cursor: pointer;
  display: block;
  font-size: 68.75%;
  color: #6e6e6e;
  position: relative;
  line-height: 18px;
  min-height: 42px;
}

.mainTopNavigation .selections nav ul.mainul li.mainli a.maina:hover + div,
.doormatWrapper:hover {
  opacity: 1 !important;
  display: block !important;
  transition: opacity 10s ease-in !important;
}

.mainTopNavigation .selections nav ul.mainul li.mainli a.maina strong {
  font-size: 163.64%;
  color: #000;
  line-height: 23px;
}

.hidden {
  display: none;
}

a:hover,
a:active,
a:focus {
  text-decoration: underline;
}
