ul.stepTracker {
  float: left;
  margin-top: 24px;
  width: 100%;
  margin-bottom: 50px;
}

ul.stepTracker.stepCount6 li.first {
  padding-left: 24px;
}
ul.stepTracker.stepCount6 li {
  width: 200px;
  height: 55px;
  display: flex;
  align-items: center;
}

ul.stepTracker li.first {
  padding-left: 23px;
  border-left: 1px solid #ddd;
}
ul.stepTracker li {
  font-size: 12px;
  padding-right: 25px;
  float: left;
  width: 17.4166em;
  background: url("../assets/bg-stepTracker2-sprite.gif") no-repeat right top;
  background-size: auto 300%;
}

ul.stepTracker li > a {
  display: block;
  line-height: 28px;
  padding: 12px 32px 11px 6px;
  text-align: center;
  text-decoration: none;
}

ul.stepProgress li a {
  display: flex;
  align-items: center;
}

ul.stepProgress li > a {
  text-align: left;
  padding-right: 0;
  background-position: 14px center;
  color: #666666;
  position: relative;
  pointer-events: none;
  cursor: default;
}

ul.stepProgress li.completed > a .progressNum {
  background-image: url("../assets/steptracker-disc-complete.png");
  background-position: left center;
  color: #ffffff;
}
ul.stepProgress li.completed .progressNum {
  color: #ffffff;
}
ul.stepProgress li .progressNum {
  color: #666666;
  display: block;
  float: left;
  font-size: 116.666%;
  font-weight: bold;
  line-height: 28px;
  min-width: 25px;
  width: 18px;
  background: url("../assets/steptracker-disc.png") no-repeat left center;
  background-size: 100% auto;
  text-align: center;
}
ul.stepTracker li > a > span {
  margin-left: 4px;
  margin-right: 8px;
}

ul.stepTracker li.lines7 > a,
ul.stepTracker li.lines6 > a,
ul.stepTracker li.lines5 > a,
ul.stepTracker li.lines4 > a,
ul.stepTracker li.lines3 > a,
ul.stepTracker li.lines2 > a {
  line-height: 14px;
}

ul.stepTracker li.last {
  padding-right: 0;
  border-right: 1px solid #ddd;
  background-position: -2px top;
}

.tabText {
  margin-left: 12px;
}

ul.stepTracker li.beforeSelected {
  background-position: right 50%;
  color: white;
}

ul.stepTracker li.selected {
  background-position: right bottom;
  color: #fff;
  background-color: #4e5055;
}

ul.stepTracker li.selected > a {
  color: #fff;
}

ul.stepTracker li.selectedLast {
  color: #fff;
  background-position: -2px bottom;
}
