* {
  font-size: 100%;
  line-height: 1;
  font-family: Arial, Helvetica, sans-serif;
  border: 0;
  margin: 0;
  outline: 0;
  padding: 0;
  text-align: left;
  vertical-align: baseline;
  box-sizing: border-box;
}

ul,
ol,
li {
  list-style: none;
}

a {
  display: block;
}

body .row {
  padding: 0px 0 20px;
  height: 1%;
}

body .row:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

body .grid_skin {
  padding: 0 0 10px;
  width: 950px;
}

input,
select,
button {
  vertical-align: middle;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100%;
  padding: 0;
  margin: 0;
  border: 0;
}

.textInput input {
  border-color: #d5d5d5 #e0e0e0 #e0e0e0;
  border-image: none;
  border-right: 1px solid #e0e0e0;
  border-style: solid;
  border-width: 1px;
  margin-right: 5px;
  padding: 0;
}

.textInput input,
.dropDown .textInput input,
.button:hover .dropDown .textInput input {
  background: none repeat scroll 0 0 #ffffff;
  font-size: 12px;
  height: 31px;
  line-height: 31px;
  padding: 0 3px 0 10px;
  text-decoration: none;
  width: 251px;
}

input,
select,
button {
  vertical-align: middle;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100%;
  padding: 0;
  margin: 0;
  border: 0;
}

body .grid_1 {
  width: 25px;
}
body .grid {
  float: left;
}
body .grid {
  position: relative;
  padding: 0 0 0 0;
}

body .grid_9 {
  width: 430px;
  padding-right: 20px;
}

body .grid_8 {
  width: 300px;
}

body .grid_24 {
  width: 950px;
}

a {
  color: #000;
}

a {
  cursor: pointer;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 0 0 1px rgb(255 255 255 / 50%);
}

.locale a.dropDownLink,
.locale .dropDownClosing a.dropDownLink,
.locale .dropDownClosed a.dropDownLink,
.locale .dropDownOpening a.dropDownLink,
.locale .dropDownOpen a.dropDownLink {
  display: block;
  text-decoration: none;
  color: #fff;
  padding: 10px 0 10px 10px;
  background: url("./assets/bg_gradient.gif") left bottom repeat-x;
}

.locale a.dropDownLink span span.flag {
  background: url("./assets/flag.gif") left 1px no-repeat;
  padding: 0 0 0 0px;
  font-size: 100%;
}

.innerPage {
  background: #fff;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  width: 940px;
  padding: 6px 10px 0;
  font-size: 62.5%;
  padding-bottom: 20px;
}

.containerStyle01 h5,
.containerStyle01 h3 {
  font-size: 16px;
  line-height: 20px;
}

a.normalIcon {
  color: #4c4c4c;
  display: table;
  font-size: 14px;
  text-decoration: none;
  margin-top: 10px;
}

.headerStyle01 a.right {
  margin-top: 6px;
}

.left {
  float: left;
}

.right {
  float: right;
}

.chericon {
  background: url("./assets/forward.gif") no-repeat scroll 0 50% transparent;
  display: inline-block;
  height: 8px;
  margin: 0 0 0 8px;
  width: 5px;
}

a span {
  cursor: pointer;
}

.containerStyle17 h4 {
  font-size: 16px;
  line-height: 16px;
  margin-bottom: 17px;
}

.question label,
.question .label {
  display: block;
  float: left;
  font-size: 14px;
  line-height: 16px;
  padding-top: 8px;
  padding-right: 30px;
  position: relative;
  text-align: right;
  width: 200px;
  color: #4e4c4c;
  font-weight: normal;
}

.containerStyle01 .question .textInput,
.containerStyle01 .question .dateInput {
  padding-left: 10px !important;
}

.textInput input {
  cursor: auto;
}

.memquestion1cam20 {
  padding-top: 10px;
}

a.linkUnderline {
  color: #333333;
  display: table;
  font-size: 14px;
  text-decoration: underline;
  margin-top: 10px;
}

.clearfix {
  min-height: 1%;
}

.clearfix:after {
  visibility: hidden;
  clear: both;
  content: ".";
  height: 0;
  display: block;
}

.textInput {
  margin-bottom: 20px;
}

a.tertiary {
  background: url("./assets/darkgrey-left.gif") no-repeat scroll left top
    transparent;
  text-decoration: none;
  white-space: nowrap;
  display: block;
  float: left;
  height: 33px;
  padding-left: 5px;
}

a.tertiary .buttonInner {
  background: url("./assets/darkgrey.gif") no-repeat scroll right top
    transparent;
  color: #fff;
  font-size: 1.5em;
  line-height: 31px;
  text-decoration: none;
  display: block;
  float: left;
  height: 34px;
  padding: 0 21px 0 12px;
}

.right div.button {
  margin-right: 0;
  margin-left: 10px;
}
div.primary {
  background: url("./assets/default-left.gif") no-repeat 0 0 rgba(255, 0, 0, 0);
  display: block;
  float: left;
  height: 34px;
  padding-left: 6px;
}

div.primary .buttonInner {
  background: url("./assets/default.gif") no-repeat 100% 0;
  font-weight: normal;
  display: block;
  float: left;
  height: 34px;
}

a.button,
div.button {
  margin-right: 10px;
  position: inherit;
}

div.primary .buttonInner input {
  background: none;
  color: #fff;
  border: none;
  font-size: 1.5em;
  line-height: 31px;
  cursor: pointer;
  height: 34px;
  margin-right: -9px;
  padding: 0 18px 2px;
  position: relative;
  left: -9px;
  overflow: visible;
  top: 0;
}

a:focus,
a:hover {
  text-decoration: underline;
  color: #db0011;
}

div.primary .buttonInner input:hover,
div.primary .buttonInner input:focus {
  color: #fff;
  text-decoration: underline;
}

a.tertiary:hover,
a.tertiary:active,
a.tertiary:focus {
  text-decoration: underline;
}

.containerStyle17 h3 {
  font-size: 18px;
  font-weight: normal;
  padding-bottom: 12px;
  line-height: 28px;
  border-bottom: 1px solid #d0d0d0;
}

input:focus:invalid {
  border-color: #7fc153;
  -moz-box-shadow: none;
}

.notValid {
  border-color: #e23320 !important;
}
