.login .loginBoxes {
  display: flex;
  justify-content: space-between;
}

.login .loginBoxes h3 {
  padding-bottom: 18px;
  margin-bottom: 28px;
  border-bottom: 1px dotted #e5e5e5;
  font-size: 1.8em;
  width: 342px !important;
}

.login .loginBox h3 {
  border-bottom: 1px dotted #e5e5e5;
  font-size: 1.8em;
  margin-bottom: 16px;
}

.login .loginBoxes .loginBox {
  width: 390px !important;
  margin: 0;
  padding-top: 28px;
}

.login .loginBox label {
  font-size: 16px;
}

.login .textInput input {
  font-size: 14px;
  width: 192px;
  height: 32px;
  line-height: 32px;
}

.login .loginBox .row {
  padding-bottom: 16px;
}

.login .loginBox .rowwidth {
  display: flex;
  width: 500px;
}

.login .loginBoxes .loginBox .buttonMargin {
  margin-left: 10px;
}

.login .loginBoxes .loginBox .button {
  background: url("../assets/default-left.gif") no-repeat scroll 0 0 #db0011;
  height: 34px;
  line-height: 34px;
  margin-right: 0;
}

.login div.primary {
  background: url("../assets/default-left.gif") no-repeat 0 0 rgba(255, 0, 0, 0);
  display: block;
  float: left;
  height: 34px;
  padding-left: 6px;
}

.login a.button,
.login div.button {
  margin-right: 10px;
  position: inherit;
}

.login .loginBoxes .loginBox .button .buttonInner {
  background: url("../assets/default.gif") no-repeat scroll 100% 0 transparent;
  height: 34px;
  line-height: 31px;
}

.login div.primary .buttonInner {
  background: url("../assets/default.gif") no-repeat 100% 0;
  font-weight: normal;
  display: block;
  float: left;
  height: 34px;
}

.login .grid_9.loginBox .button .buttonInner input {
  font-size: 1.5em;
  padding: 0 0 2px 0;
  width: 92px;
  line-height: 48px;
  height: 33px;
  line-height: 31px;
  text-align: center;
}

.login div.primary .buttonInner input {
  background: none;
  color: #fff;
  border: none;
  font-size: 1.5em;
  line-height: 31px;
  cursor: pointer;
  height: 34px;
  margin-right: -9px;
  padding: 0 18px 2px;
  position: relative;
  left: -9px;
  overflow: visible;
  top: 0;
}

.login div.primary .buttonInner input:hover,
.login div.primary .buttonInner input:focus {
  color: #fff;
  text-decoration: underline;
}

.login .loginBox .checkboxContainer {
  padding-top: 1px;
  float: left;
}

.login .checkboxContainer .dijitCheckBox {
  float: left;
  margin-top: 4px;
  margin-right: 10px;
}
.login .dijitCheckBox {
  margin-top: 5px;
  width: 17px;
  height: 17px;
  background: url("../assets/customcheckbox.gif") no-repeat left top #ccc;
  background-position: 0 0;
  overflow: hidden;
}

.login .loginBox .checkboxContainer label {
  font-size: 14px;
}
.login .checkboxContainer label {
  line-height: 24px;
}

.login .loginBox .linkList01 {
  float: left;
  margin-top: 4px;
}
.login .containerStyle08 ul {
  padding-bottom: 4px;
  padding-top: 2px;
}

.login .containerStyle08 .linkList01 li {
  float: left;
  font-size: 14px;
  margin-right: 30px;
  padding-bottom: 9px;
  width: 100%;
}
.login .loginBox .linkList01 li {
  float: left;
  font-size: 14px;
  margin-right: 30px;
}

.login .linkList01 li a {
  display: table;
  line-height: 18px;
  text-decoration: none;
  color: #333333;
}

.login .listBullets01 li a {
  color: #4d4d4d;
}

.login .linkList01 li .chevron {
  background: url("../assets/forward.gif") no-repeat right center;
  width: 12px;
  height: 8px;
  display: inline-block;
  vertical-align: middle;
}

.login .linkList01 li a:hover,
.login .linkList01 li a:focus {
  color: #db0011;
  text-decoration: underline;
}

.login .loginBoxes .grid_9.loginBox {
  width: 390px !important;
  margin: 0;
  padding-top: 28px;
}
.login .loginBox {
  padding-top: 27px;
}
.login .containerStyle08 {
  margin-top: 31px;
}

.login .loginBoxes .grid_9.loginBox h3 {
  width: 342px !important;
}
.login .loginBoxes .containerStyle08 h3 {
  padding-bottom: 18px;
  margin-bottom: 28px;
}

.login .containerStyle08 p {
  font-size: 14px;
  line-height: 21px;
  padding-bottom: 21px;
}

.login .listBullets01 li {
  background: url("../assets/bg-bullet01.gif") no-repeat left 8px;
  padding-left: 25px;
  line-height: 20px;
  font-size: 1.2em;
}

.login .containerStyle08 a:hover,
.login .containerStyle08 a:focus {
  color: #db0011;
}

.login a:focus,
.login a:hover {
  text-decoration: underline;
  color: #db0011;
}

.login img {
  vertical-align: bottom;
}

.login p {
  color: #4c4c4c;
  font-size: 14px;
  line-height: 18px;
  padding-bottom: 18px;
}
